
import {mapMutations} from 'vuex'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapState, mapActions } from 'vuex'

export default {
    components: {
        Flexbox,
        FlexboxItem,
        XButton
    },

    computed: {
        ...mapState({
            cities: state => state.app.cities,
            selectedNames: state => state.app.selectedNames,
        }),
    },

    methods: {
        ...mapMutations(['SET_TITLE']),
        ...mapActions([
            'toggleCity',
        ]),
    },

    mounted() {
        this.SET_TITLE(this.$t('Discovery'))
    },


}



